import React, { useState } from "react";
import axios from "axios";
import { Bar, Doughnut } from "react-chartjs-2";
import "chart.js/auto";

function UploadPage() {
  const [files, setFiles] = useState({
    sbyt: null,
    budget: null,
    remont: null,
  });
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);

  const handleFileChange = (e, type) => {
    setFiles({ ...files, [type]: e.target.files[0] });
  };

  const handleUpload = async () => {
    if (!files.sbyt || !files.budget || !files.remont) {
      setError(",    .");
      return;
    }

    setError(null);

    const formData = new FormData();
    formData.append("sbyt", files.sbyt);
    formData.append("budget", files.budget);
    formData.append("remont", files.remont);

    try {
      const response = await axios.post("/api/upload", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
      setData(response.data);
    } catch (err) {
      setError("  .  .");
    }
  };

  const renderSummary = () => {
    if (!data) return null;

    const doughnutData = (label, value) => ({
      labels: ["%  ", " 100%"],
      datasets: [
        {
          data: [value, 100 - value],
          backgroundColor: ["#4CAF50", "#FF5722"],
        },
      ],
    });

    const barData = (labels, plan, fact) => ({
      labels,
      datasets: [
        {
          label: "",
          data: plan,
          backgroundColor: "#2196F3",
        },
        {
          label: "",
          data: fact,
          backgroundColor: "#4CAF50",
        },
      ],
    });

    return (
      <div className="grid grid-cols-2 gap-6">
        <div className="bg-white p-4 rounded shadow">
          <h2 className="text-lg font-bold mb-4">
            %    
          </h2>
          <Doughnut
            data={doughnutData("", data.sbyt.onePhasePercent)}
          />
        </div>
        <div className="bg-white p-4 rounded shadow">
          <h2 className="text-lg font-bold mb-4">
            %    
          </h2>
          <Doughnut
            data={doughnutData("", data.sbyt.threePhasePercent)}
          />
        </div>
        <div className="bg-white p-4 rounded shadow col-span-2">
          <h2 className="text-lg font-bold mb-4"> </h2>
          <Bar
            data={barData(
              data.budget.dates,
              data.budget.plan,
              data.budget.fact
            )}
          />
        </div>
        <div className="bg-white p-4 rounded shadow col-span-2">
          <h2 className="text-lg font-bold mb-4"></h2>
          <Bar
            data={barData(
              data.remont.dates,
              data.remont.inRepair,
              data.remont.repaired
            )}
          />
        </div>
      </div>
    );
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-4 text-center"></h1>

      {/*    */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <div>
          <label className="block mb-2 font-bold"> ""</label>
          <input type="file" onChange={(e) => handleFileChange(e, "sbyt")} />
        </div>
        <div>
          <label className="block mb-2 font-bold"> ""</label>
          <input type="file" onChange={(e) => handleFileChange(e, "budget")} />
        </div>
        <div>
          <label className="block mb-2 font-bold"> ""</label>
          <input type="file" onChange={(e) => handleFileChange(e, "remont")} />
        </div>
      </div>

      <button
        onClick={handleUpload}
        className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
      >
         
      </button>

      {error && <p className="text-red-500 mt-4">{error}</p>}

      <div className="mt-8">{renderSummary()}</div>
    </div>
  );
}

export default UploadPage;
